import React,{Component} from "react";
import ReactDom from "react-dom";
import bootstrap from "bootstrap/dist/css/bootstrap.css";
import css from "./App.css"

class Form extends Component{
    render(){
        return <input placeholder={this.props.ph} className={this.props.class}></input>
    }
}

class Btn extends Component{
    render(){
        return <input type="submit" placeholder={this.props.ph} className={this.props.class}></input>
    }
}

class FForm extends Component{
    render(){
        return(
            <>
            <h1>Welcome to React Form</h1>
            <form className="ff">
                <Form ph="Name" class="nrm"/>
                <Form ph="Name" class="nrm"/>
                <br/>
                <Form ph="Email" class="long nrm"/>
                <br/>
                <Form ph="Password" class="long nrm"/>
                <br/>
                <Btn class="btn btn-primary bb"/>
            </form>
            </>
        )
    }
}

ReactDom.render(<FForm/>,document.getElementById("root"))